"use client";
import { useEffect, useState } from "react";
import { motion } from "framer-motion";
import { Button } from "@/components/ui/button";
import { toast } from "react-hot-toast";

export default function ProviderDashboard() {
  const API_BASE = process.env.NEXT_PUBLIC_API_URL || "https://khedme-api.onrender.com";

  // State
  const [provider, setProvider] = useState(null);
  const [loading, setLoading] = useState(false);
  const [uploading, setUploading] = useState(false);
  const [uploadProgress, setUploadProgress] = useState(0);
  const [applications, setApplications] = useState([]);
  const [loadingApps, setLoadingApps] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [categories, setCategories] = useState([]);
  const [districts, setDistricts] = useState([]);
  const [editData, setEditData] = useState({ full_name: "", category_id: "", district_id: "" });
  const [errors, setErrors] = useState({});
  const [dark, setDark] = useState(false);

  /* -------------------------
     Initialization: provider + meta + theme
  --------------------------*/
  useEffect(() => {
    // provider from localStorage
    const stored = localStorage.getItem("provider");
    if (stored) {
      const parsed = JSON.parse(stored);
      setProvider(parsed);
      setEditData({
        full_name: parsed.full_name || "",
        category_id: parsed.category_id || "",
        district_id: parsed.district_id || "",
      });
    }

    // meta (categories + districts)
    (async () => {
      try {
        const res = await fetch(`${API_BASE}/api/meta`);
        const data = await res.json();
        if (data.success) {
          setCategories(data.categories || []);
          setDistricts(data.districts || []);
        }
      } catch (err) {
        console.error("Failed to load meta:", err);
      }
    })();

    // theme from localStorage
    const theme = localStorage.getItem("khedme:theme");
    if (theme === "dark") {
      setDark(true);
      document.documentElement.classList.add("dark");
    } else {
      setDark(false);
      document.documentElement.classList.remove("dark");
    }

    // Listen for provider updates emitted elsewhere
    const onProviderUpdated = () => {
      const s = localStorage.getItem("provider");
      if (s) setProvider(JSON.parse(s));
    };
    window.addEventListener("providerUpdated", onProviderUpdated);

    return () => window.removeEventListener("providerUpdated", onProviderUpdated);
  }, []);

  /* -------------------------
     Dark mode toggle (subtle)
  --------------------------*/
  const toggleDark = () => {
    const newDark = !dark;
    setDark(newDark);
    if (newDark) document.documentElement.classList.add("dark");
    else document.documentElement.classList.remove("dark");
    localStorage.setItem("khedme:theme", newDark ? "dark" : "light");
  };

  /* -------------------------
     Fetch applications
  --------------------------*/
  const fetchApplications = async () => {
    if (!provider?.id) return;
    setLoadingApps(true);
    try {
      const res = await fetch(`${API_BASE}/api/providers/applications/${provider.id}`);
      const data = await res.json();
      if (data.success) setApplications(data.applications);
      else toast.error("Failed to load applications");
    } catch (err) {
      console.error("Error fetching applications:", err);
      toast.error("Error loading applications");
    } finally {
      setLoadingApps(false);
    }
  };

  useEffect(() => {
    if (provider?.id) fetchApplications();
  }, [provider]);

  /* -------------------------
     Availability toggle
  --------------------------*/
  const toggleAvailability = async () => {
    if (!provider) return toast.error("Provider not found");
    const newStatus = !provider.available;
    setLoading(true);
    try {
      const res = await fetch(`${API_BASE}/api/providers/availability`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ provider_id: provider.id, is_available: newStatus }),
      });
      const data = await res.json();
      if (data.success) {
        toast.success(data.message);
        const updated = { ...provider, available: newStatus };
        setProvider(updated);
        localStorage.setItem("provider", JSON.stringify(updated));
        // notify navbar/other windows
        window.dispatchEvent(new Event("providerUpdated"));
      } else {
        toast.error(data.message || "Failed to update availability");
      }
    } catch (err) {
      console.error("Availability error:", err);
      toast.error("Server error updating availability");
    } finally {
      setLoading(false);
    }
  };

  /* -------------------------
     Profile save with validation
  --------------------------*/
  const validateProfile = () => {
    const e = {};
    if (!editData.full_name || editData.full_name.trim().length < 2) e.full_name = "Full name is required";
    if (!editData.category_id) e.category_id = "Please select a category";
    if (!editData.district_id) e.district_id = "Please select a district";
    setErrors(e);
    return Object.keys(e).length === 0;
  };

  const handleSaveProfile = async () => {
    if (!provider) return;
    if (!validateProfile()) {
      toast.error("Fix the errors before saving");
      return;
    }

    setLoading(true);
    try {
      const res = await fetch(`${API_BASE}/api/providers/update`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          id: provider.id,
          full_name: editData.full_name,
          category_id: editData.category_id,
          district_id: editData.district_id,
        }),
      });
      const data = await res.json();
      if (data.success) {
        toast.success("Profile updated ‚úÖ");
        setProvider(data.provider);
        localStorage.setItem("provider", JSON.stringify(data.provider));
        window.dispatchEvent(new Event("providerUpdated"));
        setShowModal(false);
        // refresh apps to reflect any changes
        fetchApplications();
      } else {
        toast.error(data.message || "Failed to update profile");
      }
    } catch (err) {
      console.error("Save profile error:", err);
      toast.error("Server error updating profile");
    } finally {
      setLoading(false);
    }
  };

  /* -------------------------
     Upload with progress (XHR)
  --------------------------*/
  const handleUpload = (e) => {
    const file = e.target.files?.[0];
    if (!file || !provider) return;
    const formData = new FormData();
    formData.append("provider_id", provider.id);
    formData.append("image", file);

    setUploading(true);
    setUploadProgress(0);

    const xhr = new XMLHttpRequest();
    xhr.open("POST", `${API_BASE}/api/providers/upload-profile`);

    xhr.upload.onprogress = (ev) => {
      if (ev.lengthComputable) {
        setUploadProgress(Math.round((ev.loaded / ev.total) * 100));
      }
    };

    xhr.onload = () => {
      setUploading(false);
      try {
        if (xhr.status === 200) {
          const res = JSON.parse(xhr.responseText);
          if (res.success) {
            toast.success("Profile picture updated ‚úÖ");
            const updated = { ...provider, profile_image: res.image_url };
            setProvider(updated);
            localStorage.setItem("provider", JSON.stringify(updated));
            window.dispatchEvent(new Event("providerUpdated"));
            // refresh applications (in case)
            fetchApplications();
          } else {
            toast.error(res.message || "Upload failed");
          }
        } else {
          toast.error("Upload failed");
        }
      } catch (err) {
        console.error("Upload parse error:", err);
        toast.error("Upload failed");
      } finally {
        setUploadProgress(0);
      }
    };

    xhr.onerror = () => {
      setUploading(false);
      setUploadProgress(0);
      toast.error("Upload error");
    };

    xhr.send(formData);
  };

  /* -------------------------
     Helper: render bilingual label
  --------------------------*/
  const getCategoryLabel = (id) => {
    const c = categories.find((x) => String(x.id) === String(id));
    return c ? `${c.name} ‚Äî ${c.name_ar}` : "";
  };
  const getDistrictLabel = (id) => {
    const d = districts.find((x) => String(x.id) === String(id));
    return d ? `${d.name} ‚Äî ${d.name_ar}` : "";
  };

  /* -------------------------
     UI
  --------------------------*/
  return (
    <>
      <div className="max-w-4xl mx-auto mt-8 p-6 bg-white dark:bg-gray-900 dark:text-gray-200 shadow-md rounded-2xl border border-gray-100 dark:border-gray-800 transition-colors">
        <div className="flex items-center justify-between gap-4 mb-6">
          <div>
            <h1 className="text-2xl font-bold text-gray-800 dark:text-gray-100">Provider Dashboard</h1>
            <p className="text-sm text-gray-500 dark:text-gray-300 mt-1">Manage your profile, availability and applications</p>
          </div>

          <div className="flex items-center gap-3">
            <button
              onClick={toggleDark}
              className="px-3 py-1 rounded-md text-sm border border-gray-200 dark:border-gray-700 bg-white/60 dark:bg-gray-800/60 hover:brightness-95 transition"
            >
              {dark ? "üåô Dark" : "‚òÄÔ∏è Light"}
            </button>
            <Button onClick={() => setShowModal(true)} className="bg-orange-500 hover:bg-orange-600 text-white">
              ‚úèÔ∏è Edit Profile
            </Button>
          </div>
        </div>

        {provider ? (
          <>
            <div className="flex flex-col items-center mb-6">
              <div className="relative">
                <img
                  src={provider.profile_image || "https://via.placeholder.com/96x96.png?text=Profile"}
                  alt="Profile"
                  className="w-24 h-24 rounded-full border-4 border-orange-200 dark:border-gray-700 shadow-sm object-cover"
                />
                <div className="absolute -right-2 -bottom-2">
                  <label className="cursor-pointer inline-flex items-center gap-2 px-2 py-1 rounded-full text-xs bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 shadow-sm">
                    üì∏
                    <input type="file" accept="image/*" className="hidden" onChange={handleUpload} disabled={uploading} />
                  </label>
                </div>
              </div>

              {uploading && (
                <div className="mt-3 w-full max-w-xs">
                  <div className="text-xs text-gray-500 dark:text-gray-300 mb-1 text-center">Uploading... {uploadProgress}%</div>
                  <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2 overflow-hidden">
                    <div className="bg-orange-500 h-2 rounded-full transition-all" style={{ width: `${uploadProgress}%` }} />
                  </div>
                </div>
              )}

              <h2 className="text-lg font-semibold text-gray-800 dark:text-gray-100 mt-3">{provider.full_name}</h2>
              <p className="text-sm text-gray-500 dark:text-gray-300">(Phone hidden for privacy)</p>

              <div className="mt-4">
                <Button
                  onClick={toggleAvailability}
                  disabled={loading}
                  className={`px-5 py-2 rounded-full font-semibold transition ${
                    provider.available ? "bg-green-500 hover:bg-green-600 text-white" : "bg-gray-400 hover:bg-gray-500 text-white"
                  }`}
                >
                  {loading ? "Updating..." : provider.available ? "Available ‚úÖ" : "Unavailable ‚ùå"}
                </Button>
              </div>
            </div>

            {/* Applications */}
            <div className="mt-6">
              <div className="flex justify-between items-center mb-4">
                <h3 className="text-lg font-semibold text-gray-800 dark:text-gray-100">My Applications</h3>
                <div className="flex items-center gap-2">
                  <Button onClick={fetchApplications} className="bg-orange-500 hover:bg-orange-600 text-white" disabled={loadingApps}>
                    {loadingApps ? "Refreshing..." : "üîÑ Refresh"}
                  </Button>
                </div>
              </div>

              {loadingApps ? (
                <p className="text-gray-500 dark:text-gray-300">Loading applications...</p>
              ) : applications.length === 0 ? (
                <p className="text-gray-500 dark:text-gray-300">No job applications found.</p>
              ) : (
                <div className="grid gap-4">
                  {applications.map((app) => (
                    <motion.div
                      key={app.application_id}
                      initial={{ opacity: 0, y: 6 }}
                      animate={{ opacity: 1, y: 0 }}
                      transition={{ duration: 0.2 }}
                      className="border border-orange-100 dark:border-gray-800 rounded-xl p-4 shadow-sm hover:shadow-md transition-colors bg-white dark:bg-gray-800"
                    >
                      <div className="flex justify-between items-center mb-2">
                        <h4 className="font-semibold text-gray-800 dark:text-gray-100">{app.service}</h4>
                        <span className={`text-sm px-3 py-1 rounded-full font-medium ${app.status === "approved" ? "bg-green-100 text-green-700" : app.status === "rejected" ? "bg-red-100 text-red-700" : "bg-yellow-100 text-yellow-700"}`}>
                          {app.status}
                        </span>
                      </div>
                      <p className="text-gray-700 dark:text-gray-300 text-sm mb-2">{app.description}</p>
                      <p className="text-gray-500 dark:text-gray-400 text-xs">District: {app.district} ‚Ä¢ Budget: ${app.budget}</p>
                    </motion.div>
                  ))}
                </div>
              )}
            </div>
          </>
        ) : (
          <p className="text-gray-500 dark:text-gray-300 text-center">No provider data found.</p>
        )}
      </div>

      {/* Modal (Framer Motion slide-in, simple validation errors) */}
      {showModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center">
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="absolute inset-0 bg-black/40"
            onClick={() => setShowModal(false)}
          />
          <motion.div
            initial={{ y: 20, opacity: 0, scale: 0.98 }}
            animate={{ y: 0, opacity: 1, scale: 1 }}
            transition={{ duration: 0.22, ease: "easeOut" }}
            className="relative bg-white dark:bg-gray-900 rounded-xl shadow-xl p-6 w-full max-w-md z-60"
          >
            <h3 className="text-lg font-semibold text-gray-800 dark:text-gray-100 mb-3">Edit Profile</h3>

            {/* Picture + upload inside modal too */}
            <div className="flex flex-col items-center mb-4">
              <img
                src={provider?.profile_image || "https://via.placeholder.com/96x96.png?text=Profile"}
                alt="profile"
                className="w-24 h-24 rounded-full object-cover border border-orange-200 dark:border-gray-700 mb-2"
              />
              {!uploading ? (
                <label className="cursor-pointer text-sm text-orange-600 hover:underline">
                  üì∏ Change Profile Picture
                  <input type="file" accept="image/*" className="hidden" onChange={handleUpload} />
                </label>
              ) : (
                <div className="w-full max-w-[200px]">
                  <div className="text-xs text-gray-500 mb-1 text-center">Uploading... {uploadProgress}%</div>
                  <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2">
                    <div className="bg-orange-500 h-2 rounded-full transition-all" style={{ width: `${uploadProgress}%` }} />
                  </div>
                </div>
              )}
            </div>

            <div className="space-y-3">
              <div>
                <input
                  type="text"
                  placeholder="Full Name"
                  value={editData.full_name}
                  onChange={(e) => setEditData({ ...editData, full_name: e.target.value })}
                  className="w-full border rounded-lg px-3 py-2 bg-white dark:bg-gray-800 text-gray-900 dark:text-gray-100"
                />
                {errors.full_name && <p className="text-xs text-red-500 mt-1">{errors.full_name}</p>}
              </div>

              <div>
                <select
                  value={editData.category_id}
                  onChange={(e) => setEditData({ ...editData, category_id: e.target.value })}
                  className="w-full border rounded-lg px-3 py-2 bg-white dark:bg-gray-800 text-gray-900 dark:text-gray-100"
                >
                  <option value="">Select Category</option>
                  {categories.map((cat) => (
                    <option key={cat.id} value={cat.id}>
                      {cat.name} ‚Äî {cat.name_ar}
                    </option>
                  ))}
                </select>
                {errors.category_id && <p className="text-xs text-red-500 mt-1">{errors.category_id}</p>}
              </div>

              <div>
                <select
                  value={editData.district_id}
                  onChange={(e) => setEditData({ ...editData, district_id: e.target.value })}
                  className="w-full border rounded-lg px-3 py-2 bg-white dark:bg-gray-800 text-gray-900 dark:text-gray-100"
                >
                  <option value="">Select District</option>
                  {districts.map((d) => (
                    <option key={d.id} value={d.id}>
                      {d.name} ‚Äî {d.name_ar}
                    </option>
                  ))}
                </select>
                {errors.district_id && <p className="text-xs text-red-500 mt-1">{errors.district_id}</p>}
              </div>
            </div>

            <div className="flex justify-end gap-3 mt-5">
              <Button onClick={() => setShowModal(false)} className="bg-gray-300 hover:bg-gray-400 text-black">
                Cancel
              </Button>
              <Button onClick={handleSaveProfile} className="bg-orange-500 hover:bg-orange-600 text-white" disabled={loading}>
                {loading ? "Saving..." : "Save Changes"}
              </Button>
            </div>
          </motion.div>
        </div>
      )}
    </>
  );
}
